import React, { useState, useEffect } from 'react';

const ProductListing = () => {
  const [products, setProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [searchTerm, setSearchTerm] = useState('');
  const [sortBy, setSortBy] = useState('name');
  const [cart, setCart] = useState([]);

  const categories = ['all', 'Vegetables', 'Fruits', 'Grains', 'Pulses', 'Herbs', 'Spices', 'Dairy'];

  // Simulated product data
  const mockProducts = [
    { id: 1, name: 'Fresh Tomatoes', category: 'Vegetables', price: 35, unit: 'kg', farmer: 'Rajesh Patel', rating: 4.8, image: '', description: 'Fresh, red tomatoes harvested this morning', stock: 50, location: 'Nashik, Maharashtra', harvestDate: '2024-01-15' },
    { id: 2, name: 'Organic Spinach', category: 'Vegetables', price: 40, unit: 'kg', farmer: 'Priya Singh', rating: 4.9, image: '', description: 'Organic spinach grown without pesticides', stock: 25, location: 'Pune, Maharashtra', harvestDate: '2024-01-14' },
    { id: 3, name: 'Sweet Corn', category: 'Vegetables', price: 25, unit: 'kg', farmer: 'Suresh Reddy', rating: 4.7, image: '', description: 'Sweet and juicy corn kernels', stock: 40, location: 'Aurangabad, Maharashtra', harvestDate: '2024-01-13' },
    { id: 4, name: 'Fresh Carrots', category: 'Vegetables', price: 30, unit: 'kg', farmer: 'Lakshmi Devi', rating: 4.6, image: '', description: 'Orange carrots rich in vitamins', stock: 35, location: 'Kolhapur, Maharashtra', harvestDate: '2024-01-12' },
    { id: 5, name: 'Bananas', category: 'Fruits', price: 52, unit: 'dozen', farmer: 'Ramesh Kumar', rating: 4.5, image: '', description: 'Yellow bananas, perfect for daily consumption', stock: 30, location: 'Ratnagiri, Maharashtra', harvestDate: '2024-01-11' },
    { id: 6, name: 'Apples', category: 'Fruits', price: 150, unit: 'kg', farmer: 'Anita Sharma', rating: 4.8, image: '', description: 'Red apples from Kashmir region', stock: 20, location: 'Mumbai, Maharashtra', harvestDate: '2024-01-10' },
    { id: 7, name: 'Wheat', category: 'Grains', price: 25, unit: 'kg', farmer: 'Bharat Patel', rating: 4.4, image: '', description: 'Whole wheat grains for healthy cooking', stock: 100, location: 'Amravati, Maharashtra', harvestDate: '2024-01-09' },
    { id: 8, name: 'Rice', category: 'Grains', price: 40, unit: 'kg', farmer: 'Vijay Singh', rating: 4.6, image: '', description: 'Basmati rice with long grains', stock: 80, location: 'Nagpur, Maharashtra', harvestDate: '2024-01-08' },
    { id: 9, name: 'Lentils', category: 'Pulses', price: 97, unit: 'kg', farmer: 'Sunita Devi', rating: 4.7, image: '', description: 'Red lentils rich in protein', stock: 45, location: 'Solapur, Maharashtra', harvestDate: '2024-01-07' },
    { id: 10, name: 'Chickpeas', category: 'Pulses', price: 75, unit: 'kg', farmer: 'Raj Kumar', rating: 4.5, image: '', description: 'White chickpeas for various dishes', stock: 60, location: 'Latur, Maharashtra', harvestDate: '2024-01-06' },
    { id: 11, name: 'Fresh Mint', category: 'Herbs', price: 80, unit: 'kg', farmer: 'Meera Patel', rating: 4.9, image: '', description: 'Fresh mint leaves for chutneys and tea', stock: 15, location: 'Sangli, Maharashtra', harvestDate: '2024-01-15' },
    { id: 12, name: 'Turmeric', category: 'Spices', price: 200, unit: 'kg', farmer: 'Ganesh Rao', rating: 4.8, image: '', description: 'Pure turmeric powder for cooking', stock: 25, location: 'Gadchiroli, Maharashtra', harvestDate: '2024-01-05' }
  ];

  useEffect(() => {
    // Simulate API call
    const fetchProducts = async () => {
      setIsLoading(true);
      try {
        await new Promise(resolve => setTimeout(resolve, 1000));
        setProducts(mockProducts);
        setFilteredProducts(mockProducts);
      } catch (error) {
        console.error('Failed to fetch products:', error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchProducts();
  }, []);

  useEffect(() => {
    // Filter and sort products
    let filtered = products.filter(product => {
      const matchesCategory = selectedCategory === 'all' || product.category === selectedCategory;
      const matchesSearch = product.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           product.farmer.toLowerCase().includes(searchTerm.toLowerCase());
      return matchesCategory && matchesSearch;
    });

    // Sort products
    filtered.sort((a, b) => {
      switch (sortBy) {
        case 'price-low':
          return a.price - b.price;
        case 'price-high':
          return b.price - a.price;
        case 'rating':
          return b.rating - a.rating;
        case 'name':
        default:
          return a.name.localeCompare(b.name);
      }
    });

    setFilteredProducts(filtered);
  }, [products, selectedCategory, searchTerm, sortBy]);

  const addToCart = (product) => {
    setCart(prev => {
      const existingItem = prev.find(item => item.id === product.id);
      if (existingItem) {
        return prev.map(item =>
          item.id === product.id
            ? { ...item, quantity: item.quantity + 1 }
            : item
        );
      }
      return [...prev, { ...product, quantity: 1 }];
    });
  };

  const removeFromCart = (productId) => {
    setCart(prev => prev.filter(item => item.id !== productId));
  };

  const updateCartQuantity = (productId, quantity) => {
    if (quantity <= 0) {
      removeFromCart(productId);
      return;
    }
    setCart(prev =>
      prev.map(item =>
        item.id === productId
          ? { ...item, quantity }
          : item
      )
    );
  };

  const getCartTotal = () => {
    return cart.reduce((total, item) => total + (item.price * item.quantity), 0);
  };

  if (isLoading) {
    return (
      <div style={{ padding: '4rem 0', textAlign: 'center' }}>
        <div className="spinner"></div>
        <p>Loading fresh farm products...</p>
      </div>
    );
  }

  return (
    <div style={{ padding: '4rem 0', minHeight: '80vh' }}>
      <div className="container">
        {/* Header */}
        <div className="dashboard-header">
          <h1 className="dashboard-title"> Fresh Farm Products</h1>
          <p>Discover quality-assured produce directly from local farmers</p>
        </div>

        {/* Filters and Search */}
        <div className="card" style={{ marginBottom: '2rem' }}>
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', flexWrap: 'wrap', gap: '1rem' }}>
            <div style={{ display: 'flex', gap: '1rem', flexWrap: 'wrap' }}>
              <div>
                <label style={{ display: 'block', marginBottom: '0.5rem', fontWeight: '600', color: '#495057' }}>
                  Category:
                </label>
                <select
                  value={selectedCategory}
                  onChange={(e) => setSelectedCategory(e.target.value)}
                  className="form-input"
                  style={{ minWidth: '150px' }}
                >
                  {categories.map(cat => (
                    <option key={cat} value={cat}>
                      {cat === 'all' ? 'All Categories' : cat}
                    </option>
                  ))}
                </select>
              </div>

              <div>
                <label style={{ display: 'block', marginBottom: '0.5rem', fontWeight: '600', color: '#495057' }}>
                  Sort By:
                </label>
                <select
                  value={sortBy}
                  onChange={(e) => setSortBy(e.target.value)}
                  className="form-input"
                  style={{ minWidth: '150px' }}
                >
                  <option value="name">Name A-Z</option>
                  <option value="price-low">Price: Low to High</option>
                  <option value="price-high">Price: High to Low</option>
                  <option value="rating">Rating</option>
                </select>
              </div>

              <div>
                <label style={{ display: 'block', marginBottom: '0.5rem', fontWeight: '600', color: '#495057' }}>
                  Search:
                </label>
                <input
                  type="text"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="form-input"
                  placeholder="Search products or farmers..."
                  style={{ minWidth: '200px' }}
                />
              </div>
            </div>

            <div style={{ textAlign: 'right' }}>
              <div style={{ fontSize: '0.9rem', color: '#6c757d', marginBottom: '0.5rem' }}>
                {filteredProducts.length} products found
              </div>
            </div>
          </div>
        </div>

        {/* Products Grid */}
        <div className="grid grid-3">
          {filteredProducts.map(product => (
            <div key={product.id} className="product-card">
              <div style={{ 
                fontSize: '3rem', 
                textAlign: 'center', 
                padding: '1.5rem',
                backgroundColor: '#f8f9fa',
                borderBottom: '1px solid #e9ecef'
              }}>
                {product.image}
              </div>
              
              <div className="product-info">
                <div className="product-title">{product.name}</div>
                <div className="product-price">{product.price}/{product.unit}</div>
                <div style={{ fontSize: '0.9rem', color: '#6c757d', marginBottom: '0.5rem' }}>
                  by {product.farmer}
                </div>
                <div style={{ fontSize: '0.8rem', color: '#ffc107', marginBottom: '0.5rem' }}>
                   {product.rating}/5.0
                </div>
                <p style={{ fontSize: '0.9rem', color: '#6c757d', marginBottom: '1rem' }}>
                  {product.description}
                </p>
                
                <div style={{ fontSize: '0.8rem', color: '#6c757d', marginBottom: '1rem' }}>
                  <div> {product.location}</div>
                  <div> Harvested: {product.harvestDate}</div>
                  <div> Stock: {product.stock} {product.unit}</div>
                </div>

                <button 
                  onClick={() => addToCart(product)}
                  className="btn btn-primary"
                  style={{ width: '100%' }}
                  disabled={product.stock === 0}
                >
                  {product.stock === 0 ? 'Out of Stock' : 'Add to Cart'}
                </button>
              </div>
            </div>
          ))}
        </div>

        {filteredProducts.length === 0 && (
          <div className="card" style={{ textAlign: 'center', padding: '3rem' }}>
            <div style={{ fontSize: '3rem', marginBottom: '1rem' }}></div>
            <h3 style={{ color: '#6c757d', marginBottom: '1rem' }}>No Products Found</h3>
            <p style={{ color: '#6c757d' }}>
              Try adjusting your search criteria or browse all categories.
            </p>
          </div>
        )}

        {/* Shopping Cart Sidebar */}
        {cart.length > 0 && (
          <div style={{
            position: 'fixed',
            right: '20px',
            top: '50%',
            transform: 'translateY(-50%)',
            width: '300px',
            backgroundColor: 'white',
            borderRadius: '12px',
            boxShadow: '0 8px 30px rgba(0,0,0,0.15)',
            zIndex: 1000,
            maxHeight: '80vh',
            overflowY: 'auto'
          }}>
            <div style={{ padding: '1rem', borderBottom: '1px solid #e9ecef' }}>
              <h4 style={{ margin: 0, color: '#28a745' }}> Shopping Cart</h4>
            </div>
            
            <div style={{ padding: '1rem' }}>
              {cart.map(item => (
                <div key={item.id} style={{ 
                  padding: '0.5rem', 
                  border: '1px solid #e9ecef', 
                  borderRadius: '8px', 
                  marginBottom: '0.5rem',
                  backgroundColor: '#f8f9fa'
                }}>
                  <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <div>
                      <strong style={{ fontSize: '0.9rem' }}>{item.name}</strong>
                      <div style={{ fontSize: '0.8rem', color: '#6c757d' }}>
                        {item.price}/{item.unit}
                      </div>
                    </div>
                    <button
                      onClick={() => removeFromCart(item.id)}
                      style={{ 
                        background: 'none', 
                        border: 'none', 
                        color: '#dc3545', 
                        cursor: 'pointer',
                        fontSize: '1.2rem'
                      }}
                    >
                      
                    </button>
                  </div>
                  
                  <div style={{ display: 'flex', alignItems: 'center', gap: '0.5rem', marginTop: '0.5rem' }}>
                    <button
                      onClick={() => updateCartQuantity(item.id, item.quantity - 1)}
                      style={{ 
                        padding: '2px 8px', 
                        border: '1px solid #e9ecef', 
                        backgroundColor: 'white',
                        borderRadius: '4px',
                        cursor: 'pointer'
                      }}
                    >
                      -
                    </button>
                    <span style={{ minWidth: '20px', textAlign: 'center' }}>{item.quantity}</span>
                    <button
                      onClick={() => updateCartQuantity(item.id, item.quantity + 1)}
                      style={{ 
                        padding: '2px 8px', 
                        border: '1px solid #e9ecef', 
                        backgroundColor: 'white',
                        borderRadius: '4px',
                        cursor: 'pointer'
                      }}
                    >
                      +
                    </button>
                  </div>
                </div>
              ))}
              
              <div style={{ borderTop: '1px solid #e9ecef', paddingTop: '1rem', marginTop: '1rem' }}>
                <div style={{ display: 'flex', justifyContent: 'space-between', fontWeight: 'bold', marginBottom: '1rem' }}>
                  <span>Total:</span>
                  <span>{getCartTotal()}</span>
                </div>
                <button className="btn btn-primary" style={{ width: '100%' }}>
                  Proceed to Checkout
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default ProductListing;
